<template>
  <div class="nutrition-output">
    <h3>ÏòÅÏñëÏ†ïÎ≥¥</h3>
    <table>
      <thead>
        <tr>
          <th>ÏÑ±Î∂Ñ</th>
          <th>Ï¥ùÎüâ</th>
          <th>1Ïùº Í∏∞Ï§ÄÏπò ÎåÄÎπÑ (%)</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(value, key) in totalNutrition" :key="key">
          <td>{{ nutritionLabels[key] }}</td>
          <td>{{ formatValue(value) }}</td>
          <td>{{ getDailyValuePercent(key, value) }}%</td>
        </tr>
      </tbody>
    </table>
    <p class="footnote">‚Äª 1Ïùº ÏòÅÏñëÏÑ±Î∂Ñ Í∏∞Ï§ÄÏπò(%)Îäî 2,000kcal Í∏∞Ï§ÄÏù¥Î©∞, Í∞úÏù∏Ïùò ÌïÑÏöî Ïó¥ÎüâÏóê Îî∞Îùº Îã§Î•º Ïàò ÏûàÏäµÎãàÎã§.</p>
  </div>
</template>

<script>
export default {
  props: {
    ingredients: Array
  },
  data() {
    return {
      nutritionFactors: {
        "Ï†àÏûÑÎ∞∞Ï∂î": { carbs: 5, sugar: 1, protein: 1, fat: 0.1, sodium: 50 },
        "Î¨¥": { carbs: 6, sugar: 2, protein: 1, fat: 0.2, sodium: 30 },
        "ÎßàÎäò": { carbs: 33, sugar: 2, protein: 6, fat: 0.5, sodium: 5 },
        "Í≥†Ï∂ßÍ∞ÄÎ£®": { carbs: 55, sugar: 1, protein: 12, fat: 15, sodium: 10 },
        "Î©∏ÏπòÏï°Ï†ì": { carbs: 1, sugar: 0, protein: 10, fat: 1, sodium: 2500 },
        "ÏÉàÏö∞Ï†ì": { carbs: 3, sugar: 0, protein: 12, fat: 2, sodium: 3000 }
      },
      dailyValues: {
        carbs: 300, // g/day
        sugar: 50, // g/day
        protein: 50, // g/day
        fat: 65, // g/day
        sodium: 2000 // mg/day
      },
      nutritionLabels: {
        carbs: "ÌÉÑÏàòÌôîÎ¨º",
        sugar: "ÎãπÎ•ò",
        protein: "Îã®Î∞±Ïßà",
        fat: "ÏßÄÎ∞©",
        sodium: "ÎÇòÌä∏Î•®"
      }
    };
  },
  computed: {
    totalNutrition() {
      let totals = { carbs: 0, sugar: 0, protein: 0, fat: 0, sodium: 0 };

      this.ingredients.forEach(ing => {
        let factor = this.nutritionFactors[ing.type] || {};
        Object.keys(totals).forEach(key => {
          totals[key] += (factor[key] || 0) * (ing.percentage / 100);
        });
      });

      return totals;
    }
  },
  methods: {
    formatValue(value) {
      return value >= 1 ? `${value.toFixed(1)} g` : `${(value * 1000).toFixed(0)} mg`;
    },
    getDailyValuePercent(key, value) {
      if (this.dailyValues[key]) {
        return ((value / this.dailyValues[key]) * 100).toFixed(0);
      }
      return "-";
    }
  }
};
</script>

