<template>
  <div class="cost-output">
    <h4>ÏòàÏÉÅ Îã®Í∞Ä: {{ totalCost.toFixed(2) }} Ïõê 
      <button class="detail-btn" @click="showDetails = true">ÏûêÏÑ∏Ìûà ></button>
    </h4>

    <!-- Î™®Îã¨ Ï∞Ω -->
    <div v-if="showDetails" class="modal-overlay" @click="showDetails = false">
      <div class="modal-content" @click.stop>
        <h3>ÏÉÅÏÑ∏ Îã®Í∞Ä Ï†ïÎ≥¥</h3>
        <table>
          <thead>
            <tr>
              <th>Ïû¨Î£å</th>
              <th>Î∞∞Ìï©ÎπÑ(%)</th>
              <th>ÏõêÍ∞Ä(Ïõê/kg)</th>
              <th>ÏµúÏ¢Ö ÏõêÍ∞Ä(Ïõê)</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in costData" :key="index">
              <td>{{ item.name }}</td>
              <td>{{ item.percentage }}%</td>
              <td>{{ item.unitPrice.toLocaleString() }} Ïõê/kg</td>
              <td>{{ (item.percentage / 100 * item.unitPrice).toFixed(2).toLocaleString() }} Ïõê</td>
            </tr>
          </tbody>
        </table>
        <button class="close-btn" @click="showDetails = false">Îã´Í∏∞</button>
