</template>

<script>
export default {
  props: {
    ingredients: Array
  },
  data() {
    return {
      unitPrices: {
        "Ï†àÏûÑÎ∞∞Ï∂î": 600, "Î¨¥": 500, "ÎßàÎäò": 7000, "ÏÉùÍ∞ï": 8000, "Í≥†Ï∂ßÍ∞ÄÎ£®": 15000,
        "ÎåÄÌåå": 3000, "ÏñëÌåå": 2500, "Ï™ΩÌåå": 3500, "Í∞ì": 4500, "Î∂ÄÏ∂î": 4000,
        "Îã§ÏãúÏú°Ïàò": 5000, "Ï∞πÏåÄÌíÄ": 3000, "Î©∏ÏπòÏï°Ï†ì": 7000, "ÏÉàÏö∞Ï†ì": 9000,
        "Ìô©ÏÑùÏñ¥Ï†ì": 11000, "Í∞àÏπòÏ†ì": 12000, "Ï†ïÎ∞±Îãπ": 2000, "ÎØ∏Ïõê": 3000
      },
      showDetails: false
    };
  },
  computed: {
    costData() {
      return this.ingredients.map(ing => ({
        name: ing.type,
        percentage: ing.percentage,
        unitPrice: this.unitPrices[ing.type] || 0
      }));
    },
    totalCost() {
      return this.costData.reduce((sum, item) => sum + (item.percentage / 100 * item.unitPrice), 0);
    }
  }
};
</script>

