<template>
  <div class="taste-output">
    <h3>Îßõ ÌîÑÎ°úÌååÏùº</h3>
    <canvas ref="radarChart"></canvas>
    <div class="warnings">
      <p v-for="(warning, index) in tasteWarnings" :key="index" class="warning-text">
        ‚ö†Ô∏è {{ warning }}
      </p>
    </div>
  </div>
</template>

<script>
import { Chart, registerables } from "chart.js";
Chart.register(...registerables);

export default {
  props: {
    ingredients: Array
  },
  data() {
    return {
      baseTasteFactors: {
        "Ï†àÏûÑÎ∞∞Ï∂î": { sweetness: 2, saltiness: 6, sourness: 5, umami: 2, spiciness: 1 },
        "Î¨¥": { sweetness: 6, saltiness: 2, sourness: 2, umami: 1, spiciness: 0 },
        "ÎßàÎäò": { sweetness: 1, saltiness: 3, sourness: 1, umami: 4, spiciness: 3 },
        "ÏÉùÍ∞ï": { sweetness: 2, saltiness: 1, sourness: 3, umami: 2, spiciness: 6 },
        "Í≥†Ï∂ßÍ∞ÄÎ£®": { sweetness: 1, saltiness: 2, sourness: 1, umami: 3, spiciness: 9 },
        "ÎåÄÌåå": { sweetness: 3, saltiness: 1, sourness: 1, umami: 2, spiciness: 2 },
        "ÏñëÌåå": { sweetness: 5, saltiness: 1, sourness: 2, umami: 3, spiciness: 1 },
        "Ï™ΩÌåå": { sweetness: 2, saltiness: 1, sourness: 1, umami: 3, spiciness: 3 },
        "Í∞ì": { sweetness: 1, saltiness: 3, sourness: 3, umami: 2, spiciness: 5 },
        "Î∂ÄÏ∂î": { sweetness: 3, saltiness: 2, sourness: 2, umami: 3, spiciness: 4 },
        "Îã§ÏãúÏú°Ïàò": { sweetness: 3, saltiness: 4, sourness: 1, umami: 6, spiciness: 0 },
        "Ï∞πÏåÄÌíÄ": { sweetness: 4, saltiness: 1, sourness: 1, umami: 2, spiciness: 0 },
        "Î©∏ÏπòÏï°Ï†ì": { sweetness: 0, saltiness: 9, sourness: 2, umami: 7, spiciness: 0 },
        "ÏÉàÏö∞Ï†ì": { sweetness: 2, saltiness: 8, sourness: 2, umami: 6, spiciness: 0 },
        "Ìô©ÏÑùÏñ¥Ï†ì": { sweetness: 1, saltiness: 9, sourness: 3, umami: 8, spiciness: 0 },
        "Í∞àÏπòÏ†ì": { sweetness: 2, saltiness: 8, sourness: 2, umami: 7, spiciness: 0 },
        "Ï†ïÎ∞±Îãπ": { sweetness: 9, saltiness: 0, sourness: 0, umami: 0, spiciness: 0 },
        "ÎØ∏Ïõê": { sweetness: 1, saltiness: 2, sourness: 0, umami: 9, spiciness: 0 }
      },
      variantTasteModifiers: {
        "Ï†àÏûÑÎ∞∞Ï∂î": {
          "Ï†ÄÏóºÏ†àÏûÑ": { saltiness: -2, sourness: -1 },
          "Í≥†ÏóºÏ†àÏûÑ": { saltiness: +2, sourness: +1 }
        },
        "Í≥†Ï∂ßÍ∞ÄÎ£®": {
          "Î≥¥ÌÜµÎßõ": { spiciness: 7 },
          "Ï§ëÍ∞ÑÎßõ": { spiciness: 8 },
          "Îß§Ïö¥Îßõ": { spiciness: 9 }
        }
      },
      seasonTasteModifiers: {
        "Ï†àÏûÑÎ∞∞Ï∂î": {
          "Î¥ÑÏûëÌòï": { sweetness: +1 },
          "Í∞ÄÏùÑÏûëÌòï": { sourness: +1 }
        },
        "Î¨¥": {
          "Î¥ÑÏûëÌòï": { sweetness: +2 },
          "Í≤®Ïö∏ÏûëÌòï": { sweetness: +3, sourness: -1 }
        }
      }
    };
  },
  computed: {
    tasteProfile() {
      let totals = { sweetness: 0, saltiness: 0, sourness: 0, umami: 0, spiciness: 0 };

      this.ingredients.forEach(ing => {
        let baseFactor = this.baseTasteFactors[ing.type] || {};
        let variantFactor = this.variantTasteModifiers[ing.type]?.[ing.variant] || {};
        let seasonFactor = this.seasonTasteModifiers[ing.type]?.[ing.season] || {};

        Object.keys(totals).forEach(key => {
          let baseValue = baseFactor[key] || 0;
          let variantValue = variantFactor[key] || 0;
          let seasonValue = seasonFactor[key] || 0;

          totals[key] += (baseValue + variantValue + seasonValue) * (ing.percentage / 100);
        });
      });

      return Object.values(totals).map(val => Math.min(10, val.toFixed(2))); // 0~10 Ï†ïÍ∑úÌôî
    },
    tasteWarnings() {
      const labels = ["Îã®Îßõ", "Ïß†Îßõ", "Ïã†Îßõ", "Í∞êÏπ†Îßõ", "Îß§Ïö¥Îßõ"];
      return this.tasteProfile
        .map((val, index) => {
          if (val < 2) return `${labels[index]} Í∞íÏù¥ ÎÑàÎ¨¥ ÎÇÆÏäµÎãàÎã§. (ÌòÑÏû¨: ${val})`;
          if (val > 5) return `${labels[index]} Í∞íÏù¥ ÎÑàÎ¨¥ ÎÜíÏäµÎãàÎã§. (ÌòÑÏû¨: ${val})`;
          return null;
        })
        .filter(warning => warning !== null);
    }
  },
  mounted() {
    this.renderChart();
  },
  watch: {
    tasteProfile() {
      this.renderChart();
    }
  },
  methods: {
    renderChart() {
      if (this.chart) this.chart.destroy();
      this.chart = new Chart(this.$refs.radarChart, {
        type: "radar",
        data: {
          labels: ["Îã®Îßõ", "Ïß†Îßõ", "Ïã†Îßõ", "Í∞êÏπ†Îßõ", "Îß§Ïö¥Îßõ"],
          datasets: [{
            data: this.tasteProfile,
            fill: true,
            borderColor: "rgba(255, 99, 132, 1)",
            backgroundColor: "rgba(255, 99, 132, 0.2)"
          }]
        },
        options: {
          plugins: {
            legend: { display: false } // Î≤îÎ°Ä Ï†úÍ±∞
          },
          scales: {
            r: {
              min: 0,
              max: 10
            }
          }
        }
      });
    }
  }
};
</script>

<style scoped>
.taste-output {
  border: 1px solid #ddd;
  text-align: center;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
canvas {
  max-width: 250px;
  max-height: 250px;
}
.warnings {
  margin-top: 10px;
  padding: 5px;
  text-align: center;
}
.warning-text {
  color: red;
  font-size: 14px;
  font-weight: bold;
}
</style>
