        "Î¨¥": { variants: [], seasons: ["Î¥ÑÏûëÌòï", "Ïó¨Î¶ÑÏûëÌòï", "Í∞ÄÏùÑÏûëÌòï", "Í≤®Ïö∏ÏûëÌòï"] },
        "ÎßàÎäò": { variants: ["ÌïúÏßÄÎßàÎäò", "ÎåÄÏÑúÎßàÎäò", "ÎÇ®ÎèÑÎßàÎäò"], seasons: [] },
        "ÏÉùÍ∞ï": { variants: [], seasons: [] },
        "Í≥†Ï∂ßÍ∞ÄÎ£®": { variants: ["Î≥¥ÌÜµÎßõ", "Ï§ëÍ∞ÑÎßõ", "Îß§Ïö¥Îßõ"], seasons: [] },
        "ÎåÄÌåå": { variants: [], seasons: [] }, "ÏñëÌåå": { variants: [], seasons: [] },
